body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242526;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #dadce1;
  text-decoration: none;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
}

.back{
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-button{
  background-color: #484a4d;
  width:100px;
  height:50px;
  font-size: 2rem;
  border-radius: 10%;
  padding: 5px;
  margin: 2px;

  display:flex;
  align-items: center;
  justify-content: center;
  
  transition: filter 300ms, width 300ms, height 300ms;
}

.back-button:hover {
  filter: brightness(1.4);
  width: 120px;
  height: 60px;
}

.main-content{
  display: flex;
  align-items: center;
  justify-content: center;
}

.content{
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 10%;
  background-color: #484a4d;
  font-size: 2rem;
  color: #dadce1;
  transition: filter 300ms, width 300ms, height 300ms, font-size 300ms;
}

.content-button{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
